<template>
  <div class="main-body">
    <div class="main">
      <div class="carousel">
        <div class="inside-msg">
          <div class="img">
            <img :src="sellerInfo.avatar" alt="">
          </div>
          <div class="text">
            <h3 class="icon-huodongicon"> 当前餐桌号：2</h3>
            <h3 class="icon-huodongicon"> 优惠信息：{{youhuiInfo}}</h3>
            <h3 class="icon-huodongicon">
              当前订单总计：<span style="font-size:16px">￥{{totalCount}}</span>({{totalCount  + disMoney}} - {{disMoney}})
            </h3>
            <h3 class="icon-huodongicon"> {{finishPay ? '已支付(小票以及发票请到前台自取)' : '未支付(建议用餐完毕后再支付)'}}</h3>
          </div>
        </div>
        <div class="wrapper">
        </div>
      </div>
      <div class="order-wrapper"  ref="orderList">
        <div class="order" ref="orderContent">
          <div class="goods" v-for="(order, index1) in list" :key="index1" :class="{add: index1 !== 0}">
            <div class="good"
              v-for="(food, index2) in order"
              :key="index2">
              <!-- <div class="img float-left">
                <img :src="food.image" alt="">
              </div> -->
              <div class="msg float-left">
                <el-tag :type="colorType(food.type)">
                  <span class="name float-left">{{food.name}}</span>
                  <span class="count float-left"> × {{food.count}} </span>
                  <span class="single-count float-left" >￥{{food.count * food.price}}</span>
                </el-tag>
                <!-- <div class="price">单价：{{food.price}}</div> -->
                <!-- <div class="count"> × {{food.count}} </div> -->
                </div>
            </div>
          </div>
        </div>
        <div class="none" v-if="list.length === 0">
          <img src="static/img/暂无.png" alt="">
          <h3>当前订单还没有东西噢</h3>
        </div>
      </div>
    </div>
    <div :class="[ {middle: middle === true },'total-count']" v-if="list.length !== 0" @click="payOrder" :disavled="finishPay">
      <!-- <div class="count">合计：{{totalCount}}</div> -->
      <!-- <div class="kong" @click="middle = !middle"></div> -->
      支付
      <!-- <div class="pay-order" @click="payOrder" :disavled="finishPay"> </div> -->
      <!-- <div class="kong" @click="middle = !middle"></div> -->
    </div>
    <div class="pay-for" v-if="showPay">
      <div class="pay-main">
        <div class="pay-method" v-if="activeStep === 0">
          <div class="pay-message">支付方式：</div>
          <!-- <div class="pay-message">支付方式：支付方式只可选择一次</div> -->
          <div class="flex-pay">
            <div class="pay"
              v-for="(item ,index) in payMethods"
              :key="index"
              :class= "{ activePay:payMethod === item.method}"
              @click="changeMethod(index)">
              <span :class="item.icon"></span>
              <span class="method-text"> {{item.method}}</span>
              <div :class="{'el-icon-success':payMethod === item.method}"></div>
            </div>
          </div>
          <div class="buttons">
           <div class="cancel" @click="cancel">取消</div>
           <div class="next" @click="nextStep">确认</div>
          </div>
        </div>
        <div class="pay-method" v-if="activeStep === 1">
          <div class="pay-imme">
            <h4><span class="el-icon-arrow-left align-left float-left" @click="prevStep"></span>订单支付</h4>
            <div class="pay-mes">
              <h2>￥{{totalCount}} </h2>
              <div>
                <span class="float-left">收款方：</span>
                <span class="float-right">宜春季季红火锅店</span>
              </div>
              <div>
                <span class="float-left">商品:</span>
                <span class="float-right">订单支付</span>
              </div>
            </div>
            <div class="real-pay" @click="simulatePay">
              确认支付
            </div>
          </div>
          <!-- <div class="prev" @click="prevStep">上一步</div> -->
        </div>
        <div class="call-waiter" v-if="activeStep === 2">
            已呼叫服务员
        </div>
        <div class="call-waiter" v-if="activeStep === 3">
            支付成功,欢迎下次光临!
            <br>
            当前订单流水号：{{orderEntry}}
            <br>
            小票以及发票请到服务台索取
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
import {Loading} from 'element-ui'
import goods from '../../db.json'
export default {
  name: 'meMain',
  mounted () {
    this.sellerInfo = goods.seller.infos[0]
    this.discounts = goods.seller.supports[0]
    this.minMoney = this.discounts.catch
    this.disMoney = this.discounts.decrease
    this.$root.eventHub.$on('initShow', (yourData) => {
      this.showPay = false
    })
    this.$root.eventHub.$on('submitOrder', (yourData) => {
      console.log(yourData)
      if (yourData) {
        // console.log(yourData)
        if (yourData[0] > 0) {
          console.log(1)
          this.list.push(yourData[1])
          this.totalCount += yourData[0]
          if (this.totalCount < this.minMoney) {
            let gap = this.minMoney - this.totalCount
            this.youhuiInfo = '再购买' + gap + '即可减' + this.disMoney
          } else {
            this.totalCount -= this.disMoney
            this.youhuiInfo = '已减免' + this.disMoney + '哦'
          }
          this.$nextTick(() => {
            this._initScroll()
          })
        }
        // this.list.forEach((item))
        // this.excOrder()
      }
    })
  },
  data () {
    return {
      middle: false,
      finishPay: false,
      minMoney: 0,
      disMoney: 0,
      discounts: {},
      youhuiInfo: '暂无',
      sellerInfo: {},
      order: [],
      list: [],
      totalCount: 0,
      showPay: false,
      activeStep: 0,
      payMethod: '支付宝',
      payMethods: [
        {method: '支付宝', icon: 'icon-rectangle390'},
        {method: '微信', icon: 'icon-weixinzhifu'},
        {method: '餐后现金 / 刷卡结账', icon: 'icon-shouru-copy'}
      ],
      activeIndex1: 0,
      activeIndex2: 0,
      orderEntry: ''
    }
  },
  methods: {
    // excOrder () {
    //   let arr = []
    //   this.order.forEach((item) => {
    //     // console.log(item)
    //     item.list.forEach((item2) => {
    //       let obj = {}
    //       obj.name = item2.name
    //       obj.price = item2.price
    //       obj.count = item2.count
    //       obj.image = item2.image
    //       arr.push(obj)
    //     })
    //   })
    //   console.log(arr)
    //   // this.list = arr
    // }
    _initScroll () {
      // console.log(this.scroll)
      if (this.$refs.orderContent.offsetHeight > this.$refs.orderList.offsetHeight) {
        console.log('高度溢出')
        this.orderList = new BScroll(this.$refs.orderList, {
          click: true,
          momentum: true,
          bounce: true,
          probeType: 3
        })
        this.orderList.on('scroll', (pos) => {
          console.log(pos.y)
        })
      } else {
        // this.$nextTick(() => {
        //   console.log('高度未溢出')
        //   // this.orderList.refresh()
        // })
      }
    },
    colorType (type) {
      if (type === 'veg' || type === 'mashroom' || type === 'bean' || type === 'dryCargo') {
        return 'success'
      } else if (type === 'fish' || type === 'meat') {
        return 'danger'
      } else if (type === 'pan' || type === 'staple') {
        return 'info'
      } else {
        return 'warning'
      }
    },
    changeMethod (index) {
      this.payMethod = this.payMethods[index].method
      console.log(this.payMethod)
    },
    nextStep () {
      if (this.payMethod === '支付宝' || this.payMethod === '微信') {
        this.activeStep = 1
      } else {
        this.activeStep = 2
        setTimeout(() => {
          this.simulatePay()
        }, 500)
      }
    },
    cancel () {
      this.showPay = false
    },
    prevStep () {
      this.activeStep = 0
    },
    payOrder () {
      this.showPay = true
      this.activeStep = 0
      this.payMethod = '支付宝'
    },
    simulatePay () {
      let loadingInstance = Loading.service({
        lock: true,
        text: 'Loading'
      })
      setTimeout(() => {
        loadingInstance.close()
        this.list = []
        // this.totalCount = 0
        this.activeStep = 3
        setTimeout(() => {
          loadingInstance.close()
          // this.showPay = false
          this.finishPay = true
          this.orderEntry = '36073119950412'
        }, 2000)
      }, 500)
    }
  },
  watch: {
    'list': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        // newVal.forEach((item, index, array) => {
        //   array.forEach((item2, index2, array2) => {
        //     if (item.name === item2.name && index !== index2) {
        //       item.count.push(item2.count[0])
        //       delete array2[index2]
        //     }
        //   })
        // })
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    height: 100%;
    overflow:hidden;
    position: relative;
    .main{
      width: 100%;
      height: 100%;
      display:flex;
      flex-direction:column;
      .header-title{
        height:40px;
        line-height:40px;
        font-size:20px;
        font-weight:900;
        border-bottom:1px solid #ddd;
      }
      .carousel{
        height:120px;
        line-height:40px;
        width: 100%;
        text-align:center;
        position: relative;
        // font-size:10px;
        overflow:hidden;
        .inside-msg{
          position:absolute;
          background-color: rgba(7,17,27,0.5);
          height:100%;
          width:100%;
          left:0px;
          top:0px;
          color:#fff;
          display:flex;
          z-index:2;
          .img{
            width:60px;
            height:60px;
            margin:30px;
            img{
              width:60px;
              height:60px;
            }
          }
          .text{
            flex:1;
            line-height:30px;
            text-align:left;
            h3{
              margin:0px;
              font-size:11px;
              padding:0px;
            }
          }
        }
        .wrapper{
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          -webkit-filter: blur(5px);
          filter: blur(5px);
          z-index: 1;
          overflow:hidden;
          background: url('/static/img/季季红背景.jpg') center center;
          background-size: 100% auto;
          img{
            width:100%;
          }
        }
      }
      .order-wrapper{
        flex:1;
        overflow:hidden;
        position: relative;
        .none{
          position: absolute;
          width:160px;
          height:140px;
          left:50%;
          top:50%;
          margin-left:-80px;
          margin-top:-130px;
          font-size:12px;
          img{
            width:120px;
            height:100px;
          }
          h3{
            margin-top:10px;
          }
        }
        .order{
          padding:5px 0;
          .goods{
            position: relative;
            // padding-right:20px;
            box-sizing:border-box;
            overflow:hidden;
            margin-bottom:5px;
            border-bottom:1px dashed rgba(7,17,27,0.15);
            .good{
              // background-color:  rgba(7,17,27,0.15);
              font-size:12px;
              overflow:hidden;
              // margin-bottom: 2px;
              height:40px;
              line-height:40px;
              float:left;
              width:46%;
              margin:0 2%;
              // display:flex;
              // flex-direction:row;
              // .img{
              //   width:70px;
              //   height:60px;
              //   display:flex;
              //   flex-direction:row;
              //   justify-content:flex-start;
              //   align-items:center;
              //   // box-sizing:border-box;
              //   // padding:5px;
              //   img{
              //     width:50px;
              //     height:50px;
              //     padding:0 10px;
              //     vertical-align:middle;
              //   }
              // }
              .msg{
                // flex:1;
                // padding: 0 5px;
                width:100%;
                // display:flex;
                // flex-direction:column;
                // justify-content:flex-start;
                margin: 2px 0;
                .el-tag{
                  width:100%;
                  display:flex;
                  .count, .single-count{
                    flex:1;
                    text-align:left;
                  }
                  .name{
                    min-width:50%;
                  }
                }
                // .count{
                //   text-align:left;
                // }
              }
            }
            .active{
              // transform:scale(1, 1);
              height:60px;
              transition: all .5s linear;
              // background-color: red;
            }
          }
          // .good::after{
          //   position: absolute;
          //   content:'';
          //   top:-15px;
          //   left:50%;
          //   margin-left:-10px;
          //   width:20px;
          //   height:20px;
          //   border-radius:50%;
          //   background-color: #fff;
          // }
          // .good::before{
          //   position: absolute;
          //   content:'';
          //   bottom:-15px;
          //   left:50%;
          //   margin-left:-10px;
          //   width:20px;
          //   height:20px;
          //   border-radius:50%;
          //   background-color: #fff;
          // }
        }
      }
      // .middle{
      //   right:50%;
      //   transform:translate(50%);
      //   transition: all 1s;
      // }
      .add{
        .good{
          .msg{
            .count{
              color:$themeColor;
            }
          }
        }
      }
    }
    @keyframes myfirst{
      from {box-shadow:0 2px 12px 0 rgba(7,17,27,0.65)}
      to {box-shadow:0 2px 24px 0 rgba(7,17,27,0.65)}
    }
    .total-count{
      animation: myfirst 1.5s infinite alternate;
      // display:none;
      transition: all 1s;
      height:120px;
      width:120px;
      line-height:80px;
      background-color: rgba(7,17,27,0.65);
      color:#fff;
      opacity:0.5;
      position: absolute;
      right:-60px;
      bottom: -60px;
      border-radius:50%;
      margin-top:-60px;
      text-align:left;
      text-indent:1.5em;
      font-size:15px;
      // display:flex;
      // flex-direction:column;
      // align-items:center;
      // justify-content:center;
      // .count{
      //   // border-top:1px solid #999;
      //   // font-size:20px;
      //   // font-weight:900;
      //   flex:1;
      //   // color:$themeColor;
      // }
      .pay-order{
        font-size:15px;
        line-height:30px;
        // width:100%;
        // height:30px;
        // background-color: $themeColor;
        // border-left:1px solid #fff;
      }
    }
    .pay-for{
      position: absolute;
      left:0;
      right:0;
      top:120px;
      bottom:0;
      background-color: transparent;
      display:flex;
      justify-content:center;
      align-items:center;
      .pay-main{
        width:90%;
        height:320px;
        // margin: 40% auto;
        background-color: rgba(7,17,27,0.7);
        // opacity:1;
        color:#fff;
        // border-radius:50%;
        overflow:hidden;
        box-shadow:0 2px 12px 0 rgba(0,0,0,0.1);
        // padding:10px;
        box-sizing:border-box;
        .pay-method{
          display:flex;
          flex-direction:column;
          height:100%;
          .pay-message{
            padding:5px 0;
            height: 40px;
            line-height: 40px;
            border-bottom:1px dashed #eee;
            width:100%;
          }
          .flex-pay{
            flex:1;
            // background-color: #a8abaf;
            height:150px;
            line-height:50px;
            margin:5px 10px;
            padding:10px 0;
            display:flex;
            flex-direction:column;
            // background-color: #eee;
            .pay{
              flex:1;
              // border:1px solid rgba(7,17,27,0.35);
              border-radius:2px;
              margin: 0 5px;
              text-align:left;
              .method-text{
                margin-left:5px;
              }
              div{
                float:right;
                width:20px;
                height:20px;
                font-size:20px;
                margin:15px;
                border-radius:50%;
                // background-color: #fff;
              }
            }
            .activePay{
              // border-left-width:10px;
              // border-left-style:solid;
              // border-left-color:$themeColor;
              // border:1px solid #dcdfe6;
              span{
                //ccolor:$themeColor;
              }
            }
          }
          .buttons{
            height:20px;
            // background-color: $themeColor;
            display:flex;
            flex-direction:row;
            margin:5px 10px;
            padding:10px 0;
            color: #fff;
            .cancel{
              flex:1;
              .el-button{
                width:60%;
              }
            }
            .next{
              flex:1;
              border-left:1px solid #fff;
              .el-button{
                width:60%;
              }
            }
          }
          .pay-imme{
            height:320px;
            line-height:1.7;
            display:flex;
            flex-direction:column;
            h4{
              border-bottom:1px dashed #fff;
              padding-bottom:10px;
              line-height:41px;
              span{
                font-size:20px;
                margin-top:10px;
              }
            }
            h2{
              padding-bottom:10px;
            }
            .pay-mes{
              flex:1;
              padding: 10px 0;
              div{
                width:100%;
                height:30px;
                line-height:30px;
              }
            }
            .real-pay{
              height:40px;
              margin-top:10px;
              line-height:40px;
              // background-color: $themeColor;
              color: #fff;
            }
          }
        }
        .call-waiter{
          height:100%;
          line-height:30px;
          display:flex;
          justify-content:center;
          align-items:center;
        }
      }
    }
  }
</style>



