import Vue from 'vue'
import Router from 'vue-router'
import goods from '@/pages/goods'
import shopCar from '@/pages/shopCar'
import order from '@/pages/order'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'goods',
      component: goods
    },
    {
      path: '/shopCar',
      name: 'shopCar',
      component: shopCar
    },
    {
      path: '/order',
      name: 'order',
      component: order
    }
  ]
})



