<template>
   <div class="number-counts">
     <!-- <el-input-number v-model.number="dish.count" @change="handleChange" label="描述文字" size="mini"></el-input-number> -->
     <!-- <input type="number" v-model="dish.count"> -->
     <!-- {{item}} -->
     <transition name="fade">
       <div class="subtract" v-on:click.stop="subtract"  v-show="dish.count !== 0">
        <i :class="iconType(dish.type)[0]"></i></div>
     </transition>

     <div class="number" v-show="dish.count !== 0"><input type="text" v-model.number="dish.count" style="border:none;width:100%;text-align:center;"></div>
     <div class="add" @click.stop="add" v-if="dish.enrich === 'true'">
      <i :class="iconType(dish.type)[1]"></i>
     </div>
     <span v-if="dish.enrich === 'false'">已售完</span>
   </div>
</template>

<script>
// import '../assets/iconfont/iconfont.css'
export default {
  name: 'number-count',
  mounted () {
  },
  data () {
    return {
      // orderFoods: []
      item: ''
    }
  },
  props: {
    dish: {
      type: Object
    }
  },
  methods: {
    iconType (type) {
      if (type === 'veg') {
        return ['icon-huayecai', 'icon-luobu']
      } else if (type === 'mashroom') {
        return ['icon-nangua', 'icon-mogu']
      } else if (type === 'bean') {
        return ['icon-yumi', 'icon-qiezi']
      } else if (type === 'dryCargo') {
        return ['icon-yumi', 'icon-xiangchang']
      } else if (type === 'fish') {
        return ['icon-yu', 'icon-xia']
      } else if (type === 'meat') {
        return ['icon-rou', 'icon-jirou']
      } else if (type === 'pan') {
        return ['icon-lajiao', 'icon-dacong']
      } else if (type === 'staple') {
        return ['icon-xihongshifanqie', 'icon-luosi']
      } else if (type === 'fruit') {
        return ['icon-qiezi', 'icon-xihongshifanqie']
      } else if (type === 'drink') {
        return ['icon-xihongshifanqie', 'icon-yumi']
      }
    },
    subtract () {
      if (this.dish.count > 0) {
        this.dish.count -= 1
      } else {
        this.dish.count = 0
      }
    },
    add () {
      console.log('add')
      if (this.dish.count < 100) {
        this.dish.count += 1
      } else {
        this.dish.count = 99
      }
    },
    handleChange (number) {
      console.log(number)
    }
  }
}
</script>

<style lang="scss" scoped>
   @import '../assets/styles/variable';
   .fade-enter{
     opacity:0;
     transform:translateX(20px);
   }
   .fade-enter-active{
     transition:all 1s;
   }
   .fade-leave-active{
     transition:transform 1s;
   }
   .fade-leave-to{
     transform:translateX(20px);
   }

  .number-counts{
    height:20px;
    line-height:20px;
    .subtract{
      float:left;
      width:20px;
      height:20px;
      font-size:20px;
      color:$themeColor;
      border-radius:50%;
      text-align: center;
    }
    .add{
      float:right;
      width:20px;
      height:20px;
      font-size:20px;
      color:$themeColor;
      border-radius:50%;
      text-align: center;
    }
    .number{
      float:left;
      width:20px;
      height:20px;
      text-align: center;
    }
  }
</style>



