<template>
  <div class="main-footer">
    <el-menu
      :default-active="activeIndex"
      class="el-menu-demo"
      mode="horizontal"
      text-color="#999"
      @select="handleSelect">
      <el-menu-item index="0" :class="{active:activeIndex === '0'}">
        <i class="icon-shouyeicon"></i>
        <p slot="title">选购</p>
        <!-- <vue-star animate="animated bounceIn" color="#F05654" slot="title">
          <a slot="icon" class="icon-zhouzi" style="font-size:60px;"></a>
        </vue-star> -->
      </el-menu-item>
      <el-menu-item index="1" :class="{active:activeIndex === '1' }">
        <!-- <vue-star animate="animated bounceIn" color="#F05654" slot="title">
          <a slot="icon" class="icon-xie" style="font-size:60px;"></a>
        </vue-star> -->
        <i class="icon-jiarugouwucheicon"></i>
        <p slot="title">购物车</p>
      </el-menu-item>
      <el-menu-item index="2" :class="{active:activeIndex === '2' }">
        <i class="icon-xinfengicon"></i>
        <p slot="title">订单</p>
        <!-- <vue-star animate="animated bounceIn" color="#F05654" slot="title">
          <a slot="icon" class="icon-yu" style="font-size:60px;"></a>
        </vue-star> -->
      </el-menu-item>
    </el-menu>
  </div>
</template>

<script>
import {Loading} from 'element-ui'
export default {
  name: 'mainFooter',
  data () {
    return {
      activeIndex: '1',
      selectedGoods: [],
      firstOrder: [],
      flag1: true,
      flag2: true
    }
  },
  // props: ['foods'],
  //  确定active 页面
  mounted () {
    console.log(this.activeIndex)
    let pageArr = ['goods', 'shopCar', 'order']
    this.activeIndex = (pageArr.indexOf(this.$router.currentRoute.name)).toString()
    this.$root.eventHub.$on('goodSelect', (yourData) => {
      if (yourData) {
        this.selectedGoods = yourData
      }
    })
    this.$root.eventHub.$on('submitOrder', (yourData) => {
      if (yourData) {
        this.activeIndex = '2'
        this.handleSelect('2')
        this.firstOrder = yourData
        console.log(this.activeIndex)
      }
    })
  },
  methods: {
    //  判断选择的栏目进行跳转
    handleSelect (key) {
      switch (key) {
        case '0':
          this.$root.eventHub.$emit('initShow')
          let loadingInstance = Loading.service({
            lock: true,
            text: 'Loading'
          })
          setTimeout(() => {
            loadingInstance.close()
            this.activeIndex = '0'
            this.$router.push('/')
            this.$nextTick(() => {
              this.$root.eventHub.$emit('initScroll')
            })
          }, 200)
          break
        case '1':
          this.$root.eventHub.$emit('initShow')
          loadingInstance = Loading.service({
            lock: true,
            text: 'Loading'
          })
          setTimeout(() => {
            loadingInstance.close()
            this.activeIndex = '1'
            this.$router.push('shopCar')
            if (this.flag1) {
              this.flag1 = false
              console.log(111)
              setTimeout(() => {
                this.$root.eventHub.$emit('goodSelect', this.selectedGoods)
              }, 0)
            }
          }, 200)
          break
        case '2':
          this.$root.eventHub.$emit('initShow')
          loadingInstance = Loading.service({
            lock: true,
            text: 'Loading'
          })
          setTimeout(() => {
            loadingInstance.close()
            if (this.flag2) {
              this.flag2 = false
              console.log(222)
              setTimeout(() => {
                this.$root.eventHub.$emit('submitOrder', this.firstOrder)
              }, 0)
            }
            this.activeIndex = '2'
            this.$router.push('order')
          }, 200)
          break
        default:
          console.log('无')
      }
      // if (this.flag) {
      //   this.$root.eventHub.$emit('change', this.order)
      // }
      // setTimeout(() => {
      // }, 0)
    }
  }
}
</script>
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-footer{
    background-color: $themeColor;
    height: 60px;
    line-height: 30px;
    width: 100%;
    border-top: 1px solid #999;
    overflow:hidden;
    ul{
      width:100%;
      padding:5px 0;
      box-sizing:border-box;
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      li{
        flex:1;
        line-height:25px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items:center;
        i{
          font-size:24px;
        }
        p{
          font-size:12px;
          color:#999;
          font-weight:900;
        }
        //.text{
        //  position: absolute;
        //  bottom:5px;
        //}
        // .VueStar{
        //   position:relative;
        //   transform:scale(0.6,0.6);
        // }
        // i{
        //   margin:0px;
        // }
      }
      .active{
        color:#bc0023 !important;
      }
    }
  }
</style>



