<template>
  <div class="main-body">
    <!-- <h4>{{order}}</h4> -->
    <div class="show-foods">
     <!--  <div class="header-title">
        购物车
      </div> -->
      <div class="carousel">
        <div class="inside-msg">
          <div class="img">
            <img :src="sellerInfo.avatar" alt="">
          </div>
          <div class="text">
            <!-- <h3 class="icon-huodongicon"> 当前餐桌号:6号</h3> -->
            <h3 class="icon-huodongicon"> 购物车小计: ￥ {{totalMoney}}</h3>
            <h3 class="icon-huodongicon"> 上餐排位人数：3位</h3>
            <h3 class="icon-huodongicon"> 预计到餐时间：10分钟</h3>
          </div>
        </div>
        <div class="wrapper">
          <!-- <img src="/static/img/季季红背景2.jpg" alt=""> -->
        </div>
      </div>
      <div class="none" v-if="totalMoney === 0">
        <img src="static/img/暂无.png" alt="">
        <h3>当前购物车还没有东西噢</h3>
      </div>
      <el-collapse v-model="activeNames" @change="handleChange" accordion>
        <div class="food" v-for="(item, index) in order" :key="index" ref="order">
          <el-collapse-item :name="index" v-if="item.list.length > 0">
            <template slot="title">
               <span class="type-name">{{item.typeName}}</span>
               <span class="num-radius">{{item.list.length}}</span>
            </template>
            <div class="dish" v-for="dish in item.list" :key="dish.name">
              <div class="img">
                <img :src="dish.image" alt="">
              </div>
              <div class="msg">
                <p class="name">{{dish.name}}</p>
                <div class="count-num">
                  <span class="price">￥{{dish.price}}</span>
                  <number-count :dish="dish"></number-count>
                </div>
              </div>
            </div>
            <div class="no-food" v-if="item.list.length === 0  ">
              尚未点购
            </div>
          </el-collapse-item>
        </div>
      </el-collapse>
      <div class="money" v-if=" order.length !== 0">
        <!-- <div class="counts">
          <div class="number float-left">合计:  ￥{{totalMoney}}</div>
        </div> -->
        <div class="count-button" @click="submitOrder">
          <span class="text-submit">提交</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import numberCount from './inputNumber.vue'
import goods from '../../db.json'
export default {
  name: 'orderMain',
  mounted () {
    this.sellerInfo = goods.seller.infos[0]
    this.$root.eventHub.$on('goodSelect', (yourData) => {
      if (yourData) {
        console.log(yourData)
        // if (yourData)
        this.order = yourData
        this.exchange()
        this.countMoney()
      }
    })
  },
  data () {
    return {
      sellerInfo: {},
      activeNames: ['1'],
      order: [],
      singleOrder: [],
      totalMoney: 0
    }
  },
  methods: {
    exchange () {
      this.singleOrder = []
      this.singleOrder.push(...this.order)
      let arr = []
      this.singleOrder.forEach((item) => {
        item.list.forEach((item2) => {
          let obj = {}
          obj.name = item2.name
          obj.price = item2.price
          obj.count = item2.count
          obj.type = item.type
          // obj.count.push(item2.count)
          obj.image = item2.image
          arr.push(obj)
        })
      })
      this.singleOrder = arr
      console.log(this.singleOrder)
    },
    countMoney () {
      this.totalMoney = 0
      this.order.forEach((item) => {
        item.list.forEach((item2) => {
          let _thisMoney = item2.price * item2.count
          this.totalMoney += _thisMoney
        })
      })
    },
    handleChange (val) {
      console.log(val)
    },
    payOrder () {
      this.showPay = true
    },
    submitOrder () {
      console.log(this.order)
      let arr = [this.totalMoney, this.singleOrder]
      this.$root.eventHub.$emit('submitOrder', arr)
      this.$nextTick(() => {
        this.order = []
        this.totalMoney = 0
        this.$root.eventHub.$emit('clearCar')
      })
      this.$router.push('order')
    }
  },
  components: {
    numberCount
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .el-collapse-item__content{
    padding:0px !important;
  }
  .main-body{
    width: 100%;
    position: relative;
    .show-foods{
      width:100%;
      height:100%;
      box-sizing:border-box;
      border-radius:5px;
      background-color: #fff;
      position:relative;
      display:flex;
      flex-direction:column;
      position: relative;
      overflow:hidden;
      .carousel{
        height:120px;
        line-height:40px;
        width: 100%;
        text-align:center;
        position: relative;
        overflow:hidden;
        .inside-msg{
          position:absolute;
          background-color: rgba(7,17,27,0.5);
          height:100%;
          width:100%;
          left:0px;
          top:0px;
          color:#fff;
          z-index:2;
          display:flex;
          .img{
            width:60px;
            height:60px;
            margin:30px;
            img{
              width:60px;
              height:60px;
            }
          }
          .text{
            flex:1;
            line-height:40px;
            text-align:left;
            h3{
              margin:0px;
              font-size:11px;
              padding:0px;
            }
          }
        }
        .wrapper{
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          -webkit-filter: blur(3px);
          filter: blur(3px);
          z-index: 1;
          overflow:hidden;
          background: url('/static/img/季季红背景2.jpg') center center;
          background-size: 100% auto;
          img{
            width:100%;
          }
        }
      }
      .none{
        position: absolute;
        width:160px;
        height:140px;
        left:50%;
        top:50%;
        margin-left:-80px;
        margin-top:-70px;
        font-size:12px;
        img{
          width:120px;
          height:100px;
        }
        h3{
          margin-top:10px;
        }
      }
      .el-collapse{
        flex:1;
        border:none;
        text-indent:1em;
        overflow:auto;
        .food{
          text-align: left;
          padding:0 5px;
          .type-name{
            text-align:left;
            text-indent:0;
            display:inline-block;
            min-width:60px;
          }
          .num-radius{
            display:inline-block;
            line-height:20px;
            background-color:$themeColor;
            width:20px;
            height:20px;
            text-align:center;
            text-indent:0;
            border-radius:50%;
            color:#fff;
            vertical-align:baseline;
            margin-left:10px;
          }
          .dish{
            display:flex;
            flex-direction:row;
            // border-bottom:1px dashed rgba(7,17,27,0.65);
            .img{
              height:60px;
              width:60px;
              padding: 5px 0;
              img{
                height:60px;
                width:60px;
              }
            }
            .msg{
              flex:1;
              height:70px;
              padding:2px 5px;
              box-sizing:border-box;
              display:flex;
              flex-direction:column;
              justify-content:space-between;
              .name{
              }
              .price{
              }
              .count-num{
                .number-counts{
                  float:right;
                  width:60px;
                  line-height:20px;
                  text-indent:0em;
                }
              }
            }
          }
          .no-food{
          }
        }
      }
      @keyframes myfirst{
        from {box-shadow:0 2px 12px 0 rgba(7,17,27,0.65)}
        to {box-shadow:0 2px 24px 0 rgba(7,17,27,0.65)}
      }
      .money{
        width:120px;
        height:120px;
        // height: 40px;
        // line-height: 40px;
        box-sizing:border-box;
        // line-height:60px;
        position: absolute;
        right:-60px;
        bottom:-60px;
        z-index:10;
        font-size:15px;
        margin-left:-60px;
        display:flex;
        text-align:left;
        text-indent:1.5em;
        .count-button{
          animation: myfirst 1.5s infinite alternate;
          background-color: rgba(7,17,27,0.75);
          opacity:0.5;
          color:#fff;
          font-size:14px;
          font-weight:900;
          width:100%;
          height:120px;
          border-radius:50%;
          line-height:80px;
        }
        // .counts{
        //   border-top: 1px solid #999;
        //   flex:1;
        //   .number{
        //     width:100px;
        //   }
        // }
      }
    }
  }
</style>



