<template>
  <div class="main-body">
    <div class="carousel">
      <el-carousel indicator-position="none" :interval="5000" arrow="always" height="120px">
        <el-carousel-item>
          <div class="seller-info">
            <div class="img float-left">
              <img :src="sellerInfo.avatar" alt="">
            </div>
            <div class="info float-left">
              <div><h3 class="text-hidden icon-huodongicon"> {{sellerInfo.name}}</h3></div>
              <div><h3 class="text-hidden icon-huodongicon"> {{sellerInfo.address}}</h3></div>
              <div><h3 class="text-hidden icon-huodongicon"> {{sellerInfo.time}}</h3></div>
              <div><h3 class="text-hidden icon-huodongicon"> {{sellerInfo.baoxiao}}</h3></div>
            </div>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="youhuixinxi">
            <div class="img float-left">
              <img :src="sellerInfo.avatar" alt="">
            </div>
            <div class="info float-left">
              <div><h3></h3></div>
              <div><h3 class="text-hidden icon-huodongicon"> 今日优惠活动：</h3></div>
              <div><h3 class="text-hidden icon-huodongicon"> {{promotion.description}}</h3></div>
              <div><h3></h3></div>
            </div>
          </div>
        </el-carousel-item>
        <el-carousel-item>
          <div class="tuijian">
            <div class="img float-left">
              <img :src="sellerInfo.avatar" alt="">
            </div>
            <div class="info float-left">
              <div><h3 class="icon-huodongicon"> 今日推荐:</h3></div>
              <div v-for="item in recommend" :key="item"><h3 class="text-hidden icon-huodongicon"> {{item}}</h3></div>
            </div>
          </div>
        </el-carousel-item>
      </el-carousel>
      <div class="wrapper">
        <!-- <img src="/static/img/季季红背景3.jpg" alt=""> -->
      </div>
    </div>
    <div class="goods">
        <div class="good-nav">
          <div class="type"
          v-for="(item, index) in goodList"
          :key="item.type"
          ref="menuList"
          :class="{active: activeType === index}">
            <span @click="selectMenu(index, $event)"> {{item.typeName}}</span>
          </div>
        </div>
        <div class="box" ref="goodsWrapper">
          <div class="good-list">
            <div class="good-module"
            v-for="(item, index) in goodList"
            :key="index"
            ref="goodList">
              <div class="title">{{item.typeName}}:</div>
              <div class="dishes" v-for="dish in item.list" :key="dish.name">
                <div class="img">
                  <img v-lazy="dish.image" alt=""  @click="goodDetail(dish)">
                </div>
                <div class="msg">
                  <div class="name">{{dish.name}}
                    <!-- <span class="tag" v-for="(tag, index) in dish.tag" :key="index">{{tag}}</span> -->
                    <!-- <span class="tag" v-if="dish.enrich === 'false'">暂无库存</span> -->
                    <el-tag size="mini"  type="danger" v-for="(tag, index) in dish.tag" :key="index">{{tag}}</el-tag>
                    <!-- <span class="icon-remenicon" ></span> -->
                    <!-- <el-tag size="mini" type="success" v-if="dish.enrich === 'false'"> 暂无库存 </el-tag> -->
                  </div>
                  <div class="detail" @click="goodDetail(dish)">{{dish.detail}}</div>
                  <div class="type" @click="goodDetail(dish)">月销量：{{dish.sellCount}}</div>
                  <div class="price" @click="goodDetail(dish)">单价：￥{{dish.price}}</div>
                  <div class="count">
                   <number-count :dish="dish"></number-count>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
    </div>
    <transition name="fade">
      <div class="good-detail"  v-if="showDetail" key="showDetail">
        <div class="top">
          <span class="el-icon-arrow-left" @click="showDetail = false"></span>
          <img v-lazy="currentgood.image" alt="">
        </div>
        <div class="evaluate">
          <div class="name">{{currentgood.name}}</div>
          <div class="sale">月售： {{currentgood.sellCount}}</div>
          <div class="price">￥{{currentgood.price}}</div>
          <div class="count">
            <number-count :dish="currentgood"></number-count>
          </div>
        </div>
        <div class="good-describe">
          <p>{{currentgood.detail}}</p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import numberCount from './inputNumber.vue'
import goods from '../../db.json'
export default {
  name: 'homeMain',
  data () {
    return {
      msg: '商品详情',
      showDetail: false,
      goodList: [],
      scrollY: 0,
      listHeight: [],
      orderGoods: [],
      currentgood: {},
      activeIndex: 0,
      promotion: [],
      sellerInfo: [],
      recommend: []
    }
  },
  components: {
    numberCount
  },
  mounted () {
    //  初始化商品列表
    let goodList = goods.goods
    this.promotion = goods.seller.supports[0]
    this.sellerInfo = goods.seller.infos[0]
    this.recommend = goods.seller.recommend
    console.log(this.sellerInfo)
    // this.$nextTick(() => {
    goodList.forEach((item) => {
      item.list.forEach((item2) => {
        item2.count = 0
        item2.type = item.type
      })
    })
    this.goodList = goodList
    // })
    // this.$root.eventHub.$emit('change', this.orders)
    this.$nextTick(() => {
      this._initScroll()
      this._calculateHeight()
    })
    this.$root.eventHub.$on('clearCar', (yourData) => {
      // if (1) {
      this.goodList.forEach((item) => {
        item.list.forEach((item2) => {
          item2.count = 0
        })
      })
    })
    this.$root.eventHub.$on('initShow', (yourData) => {
      this.showDetail = false
    })
    this.$root.eventHub.$on('initScroll', (yourData) => {
      this.$nextTick(() => {
        console.log('initsCROLL')
        this.goodsWrapper.refresh()
        this.scrollY = 0
      })
    })
    console.log(goods)
  },
  methods: {
    formatter (row, column) {
      return row.price
    },
    _initScroll () {
      // console.log(this.scroll)
      // if (!this.scroll) {
      this.goodsWrapper = new BScroll(this.$refs.goodsWrapper, {
        click: true,
        momentum: true,
        bounce: true,
        probeType: 3
      })
      // } else {
      // this.goodsWrapper.refresh()
      // };
      this.goodsWrapper.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
      })
    },
    selectMenu (index) {
      // if (!event._constructed) {
      //   return
      // }
      this.activeIndex = index
      let dom = this.$refs.goodList[index]
      // console.log(dom)
      //  设置滚动时间
      this.goodsWrapper.scrollToElement(dom, 300)
    },
    _calculateHeight () {
      let goodList = this.$refs.goodList
      let height = 0
      this.listHeight.push(height)
      for (let i = 0; i < goodList.length; i++) {
        height += goodList[i].clientHeight
        this.listHeight.push(height)
      }
    },
    goodDetail (dish) {
      console.log(dish)
      this.showDetail = true
      this.currentgood = dish
    }
  },
  computed: {
    activeType () {
      console.log(this.scrollY)
      for (let i = 0; i < this.listHeight.length; i++) {
        if (!(this.listHeight[i + 1]) || (this.scrollY >= this.listHeight[i] && this.scrollY < this.listHeight[i + 1])) {
          return i
        }
      }
    },
    orders () {
      let arr = []
      this.goodList.forEach((item) => {
        let obj = {list: [], type: item.type, typeName: item.typeName}
        item.list.forEach((item2, index, array) => {
          if (item2.count > 0) {
            obj.list.push(item2)
          }
        })
        arr.push(obj)
      })
      return arr
    }
  },
  watch: {
    'orders': {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        let flag = false
        newVal.forEach((item) => {
          if (item.list.length > 0) {
            flag = true
          }
        })
        if (flag) {
          this.$root.eventHub.$emit('goodSelect', newVal)
        }
        // this.$emit('aceptGoods', newVal)
      },
      deep: true
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  @import '../assets/styles/variable';
  .main-body{
    width: 100%;
    display:flex;
    flex-direction:column;
    position: relative;
    .carousel{
      height:120px;
      width: 100%;
      text-align:left;
      position: relative;
      font-size:10px;
      overflow:hidden;
      .el-carousel__item{
        background-color: rgba(7,17,27,0.5);
        color:#fff;
        div{
          width:100%;
          height:120px;
          line-height:30px;
          display:flex;
          font-size:12px;
          .img{
            width:60px;
            height:60px;
            margin:30px;
            img{
              width:60px;
              height:60px;
            }
          }
          .info{
            flex:1;
            display:flex;
            height:120px;
            flex-direction:column;
            justify-content:sapce-between;
            overflow:hidden;
            div{
              overflow:hidden;
              padding:0 5px 0 0;
              box-sizing:border-box;
              h3{
                flex:1;
                font-size:11px;
                line-height:30px;
              }
            }
          }
        }
      }
      .wrapper{
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        -webkit-filter: blur(4px);
        filter: blur(4px);
        z-index: 1;
        overflow:hidden;
        background: url('/static/img/季季红背景3.jpg') center center;
        background-size: 100% auto;
      }
    }
    .goods{
      flex:1;
      width:100%;
      overflow:hidden;
      border-top:1px solid #eee;
      background-color: #fff;
      text-align:center;
      display:flex;
      flex-direction:row;
      position:relative;
      .good-nav{
        width:80px;
        height:100%;
        background-color: #fff;
        .type{
          font-size:13px;
          height:40px;
          width:100%;
          text-overflow: ellipsis;
          border-bottom:1px solid #fff;
          box-sizing:border-box;
          line-height:40px;
        }
        .active{
          color: $themeColor;
        }
      }
      .box{
        width:100%;
        height:auto;
        .good-list{
          flex:1;
          overflow:auto;
          .good-module{
            width:100%;
            min-height:50px;
            .title{
              line-height:40px;
              text-align:left;
              width:100%;
              padding:0 5px;
            }
            .dishes{
              height:80px;
              width:100%;
              overflow:hidden;
              border-bottom:1px solid #eee;
              padding:0 5px;
              box-sizing:border-box;
              text-align:left;
              display:flex;
              flex-direction:row;
              .img{
                height:70px;
                width:70px;
                padding: 5px 0;
                img{
                  height:70px;
                  width:70px;
                }
              }
              .msg{
                flex:1;
                padding: 0 0 0 5px;
                font-size:12px;
                .name{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                  .el-tag{
                    font-size:10px;
                    height:16px;
                    line-height:16px;
                    margin-top:2px;
                    // border:none;
                    // background-color: #fff;
                    // background-color: $themeColor;
                    // border-radius:5px;
                    // padding:0 2px;
                    // margin-left:5px;
                    // font-size:10px;
                    // color:#fff;
                  }
                }
                .sell{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                }
                .detail{
                  height:20px;
                  line-height:20px;
                  width:100%;
                  text-overflow: ellipsis;
                  white-space:no-wrap;
                  overflow:hidden;
                }
                .price{
                  height:20px;
                  line-height:20px;
                  float:left;
                }
                .count{
                  float:right;
                  width:60px;
                  line-height:20px;
                }
              }
            }
          }
        }
      }
    }
    .good-detail{
      position: absolute;
      top:0;
      bottom:0;
      left:0;
      right:0;
      box-sizing:border-box;
      overflow-y:scroll;
      z-index:11;
      border-raduis:4px;
      background-color: rgba(255,255,255,1);
      display:flex;
      flex-direction:column;
      border-bottom:1px solid #999;
      .top{
        border-bottom:1px dashed #eee;
        img{
          width:100%;
        }
        span{
          position:absolute;
          left:10px;top:10px;
          border-radius:50%;
          background-color:rgba(0,0,0,0.2);
          font-size:20px; padding:5px;
        }
      }
      .evaluate{
        padding:10px;
        text-align:left;
        border-bottom:1px solid #eee;
        .name{
          font-size:16px;
          padding:5px 0;
        }
        .sale{
          font-size:12px;
          padding:5px 0;
        }
        .price{
          height:20px;
          padding:5px 0;
          line-height:20px;
          float:left;
          color:$themeColor;
        }
        .count{
          float:right;
          padding:5px 0;
          width:60px;
          line-height:20px;
        }
      }
      .good-describe{
        padding:10px 5px;
        text-align:justify;
        text-indent:2em;
        font-size:14px;
        line-height:1.7;
        overflow: auto;
        flex:1;
        p{
        }
      }
    }
  }
</style>



